@import url(//db.onlinewebfonts.com/c/9a4b138bd9c8e1eba20dbd6a0bb1d832?family=Cooper+Black);

* {
    margin: 0;
    padding: 0;
}

html, body {
    margin: 0;
    height: 100%;
}

#crawl {
    visibility: hidden !important;
    color: #fff !important;
}
#headerImg:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

img {
    opacity: 100% !important;
}

@media only screen and (max-width: 633px) {
    #headerImg:hover {
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
    }
}
.counter-bubble {
    background-color: rgba(231, 117, 117, .95);
    right: 20px;
    bottom: 4vh;
}

@media only screen and (max-width: 633px) {
    .counter-bubble {
        bottom: 5.5vh;
        right: 3px;
    }
}
.shopping-cart {
    display: block;
    cursor: pointer;
}

.cart-image {
    width: 100px;
    height: 100px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    position: absolute;
    right: 0px;
    top: 4vh;
    -webkit-animation: playCart 0.7s steps(3) infinite;
            animation: playCart 0.7s steps(3) infinite;
    z-index: 30;
}

@-webkit-keyframes playCart {
    100% { background-position: -300px; }
}

@keyframes playCart {
    100% { background-position: -300px; }
}

@media only screen and (max-width: 633px) {
    .cart-image {
        top: 2vh;
        right: -20px;
    }
}
.wrapper{
    background: #fec0d3;
    width: 100vw;
}

.home{
    background: #fec0d3;
    width: 100%;
    height: auto;
    padding-top: calc(8vh + 40px);
    display: flex;
    top: 0;
    position: absolute;
    justify-content: center;
    align-items: flex-start;
    background-size: contain;
}

.cloud_wrapper{
    max-width: 100vw;
    display: flex;
    align-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
}

.clouds{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    margin-left: auto;
    margin-right: auto;
    margin-top: -2%;
    margin-bottom: -2%;
    overflow: hidden;
    z-index: 10;
}

.text{
    position: relative;
    z-index: 20;
}

#cloud1{
    width: 500px;
    height: 364px;
    background: no-repeat center center cover;
    -webkit-animation: playClouds1 0.8s steps(3) infinite;
            animation: playClouds1 0.8s steps(3) infinite;
}

@-webkit-keyframes playClouds1{
    100% { background-position: -1500px; }
}

@keyframes playClouds1{
    100% { background-position: -1500px; }
}

#music_text{
    width: 495px;
    height: 273px;
    top: 20px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-animation: playMusicText 1.2s steps(11) infinite;
            animation: playMusicText 1.2s steps(11) infinite;
}

#music_text:hover{
    width: 500px;
    height: 164px;
    top: 80px;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    cursor: pointer;
    -webkit-animation: playMusicHover 0.6s steps(3) infinite;
            animation: playMusicHover 0.6s steps(3) infinite;
}

@-webkit-keyframes playMusicText{
    100% { background-position: -5500px; }
}

@keyframes playMusicText{
    100% { background-position: -5500px; }
}

@-webkit-keyframes playMusicHover{
    100% { background-position: -1500px; }
}

@keyframes playMusicHover{
    100% { background-position: -1500px; }
}
/* endmusic cloud */

/* merch cloud */
#cloud2{
    width: 500px;
    height: 364px;
    -webkit-animation: playClouds2 0.8s steps(3) infinite;
            animation: playClouds2 0.8s steps(3) infinite;
}

@-webkit-keyframes playClouds2{
