/* goodies cloud */
#cloud3{
    width: 500px;
    height: 462px;
    -webkit-animation: playClouds3 0.8s steps(4) infinite;
            animation: playClouds3 0.8s steps(4) infinite;
}

@-webkit-keyframes playClouds3{
    100% { background-position: -2000px; }
}

@keyframes playClouds3{
    100% { background-position: -2000px; }
}

#goodies_text{
    width: 500px;
    height: 292px;
    top: 100px;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    -webkit-animation: playGoodiesText 1.5s steps(13) infinite;
            animation: playGoodiesText 1.5s steps(13) infinite;
}

#goodies_text:hover{
    width: 500px;
    height: 308px;
    top: 90px;
    left: 15px;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    cursor: pointer;
    -webkit-animation: playGoodiesHover 0.5s steps(3) infinite;
            animation: playGoodiesHover 0.5s steps(3) infinite;
}

@-webkit-keyframes playGoodiesText{
    100% {background-position: -6500px;}
}

@keyframes playGoodiesText{
    100% {background-position: -6500px;}
}

@-webkit-keyframes playGoodiesHover{
    100% {background-position: -1500px;}
}

@keyframes playGoodiesHover{
    100% {background-position: -1500px;}
}

/* phone */
@media only screen and (max-width: 633px){
    .home{
        justify-content: flex-start;
        width: auto;
    }

    .clouds {
        -webkit-transform: scale(0.6);
                transform: scale(0.6);
        margin: -15% -20px;
    }
}
*{
    padding: 0px;
    margin: 0px;
}

.music{
    width: 100%;
    height: auto;
    padding-top: calc(8vh + 80px);
    padding-bottom: 2vh;
    margin-left: 0;
    margin-right: 0;
    display: flex;
    flex-direction: column;
    top: 0;
    position: absolute;
    background-color: #050505;
    letter-spacing: 1px;
}

a{
    text-decoration: none;
    color: #ffffff;
}

.entry{
    /* max-height: 70%; */
    max-width: 50%;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    /* transform: scale(0.7); */
    /* transform-origin: center; */
    position: relative;
}

.items{
    display: flex;
    flex-direction: column;
    position: relative;
}

.album-art{
    /* max-width: calc(500px * .7);
    max-height: calc(500px * .7); */
    max-width: 70%;
    max-height: 70%;
    width: auto;
    height: auto;
    /* transform: scale(0.7); */
    margin-left: auto;
    margin-right: auto;
}

.streaming-logos {
    width: 70%;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
    margin-top: 10px;
    flex-direction: row;
    justify-content: space-evenly;
    align-self: auto;
}

.logo {
    width: 100%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
}

.logo:hover{
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.song-link {
    color: #ffffff;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: normal;
    font-size: calc(1.4em * .7);
    margin-top: 0.3em;
    margin-bottom: 0.3em;
    margin-left: 15%;
    text-align: left;
}

.song-link:hover {
    font-size: calc(1.4em * .75);
    margin-top: calc(0.3em * -1.5);
    margin-bottom: calc(0.3em * -1.5);
}

.song-list {
    color: #ffffff;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: normal;
    font-size: calc(1.4em * .7);
    margin-top: 0.3em;
    margin-bottom: 0.3em;
    margin-left: 15%;
    text-align: left;
}

.song-list-p {
    margin-top: 0.3em;
    margin-bottom: 0.3em;
}

.album-head {
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 15%;
    margin-right: auto;
    text-align: left;
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 600;
    font-size: auto;
}

/* phone */
@media only screen and (max-width: 633px){
    body{
        justify-content: flex-start;
    }

    .music{
        padding-top: calc(8vh + 40px);
        width: 100%;
        margin: 0 auto 0 auto;
    }

    .streaming-logos {
        width: 100%;
    }

    .entry{
        max-width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-content: center;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
        -webkit-transform: scale(1);
                transform: scale(1);
        margin-top: 20px;
    }

    .album-art{
        max-width: 70%;
        max-height: 70%;
        width: auto;
        height: auto;
        margin-left: 15%;
        margin-right: 15%;
    }

    .items{
        display: flex;
        flex-direction: column;
    }

    .album-head {
        margin-left: 15%;
        margin-right: auto;
    }

    p {
        font-size: 1em;
        margin-top: 0.2em;
        margin-bottom: 0.2em;
    }

    .song-list {
        font-size: 1em;
        margin-top: 0.2em;
        margin-bottom: 0.2em;
        margin-left: 15%;
    }

    .song-link{
        font-size: 1em;
        margin-top: 0.2em;
        margin-bottom: 0.2em;
        margin-left: 15%;
    }
    
    .song-link:hover{
        font-size: 1.1em;
        margin-top: 0.2em;
        margin-bottom: 0.2em;
    }

    .streaming-logos{
        margin-top: 5px;
        margin-bottom: 5px;
        -webkit-transform: scale(.7);
                transform: scale(.7);
    }
}
.lyrics {
    top: 0;
    position: absolute;
    width: 100%;
}

.misc-img {
    width: auto;
    height: auto;
    max-width: 70%;
    max-height: 70%;
    margin: 20px auto 20px auto;
}

.lines {
    font-size: 1.4em;
}
.goodie-head {
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 600;
    font-size: auto;
}

.album-art-goodies {
    max-width: 70%;
    max-height: 70%;
    width: auto;
    height: auto;
    margin-left: auto;
    margin-right: auto;
}

.items-goodies {
    display: flex;
    flex-direction: column;
    position: relative;
}

.goodies-entry {
    max-width: 50%;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    position: relative;
    display: flex;
    flex-direction: column;
}
.price-preview {
    background-color: rgba(21, 77, 58, 0.9);
}

.preview-header {
    cursor: pointer;
}

@media only screen and (min-width: 633px) {

    .price-preview:hover {
        background-color: rgba(48, 138, 108, 0.8) !important;
        color: #eedd4a !important;
        cursor: pointer;
    }

}

.checkout-footer {
    height: 8vh;
    top: 92vh;
}

.total-container {
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 1.5em;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    flex-wrap: wrap;
}

.total-text{
    position: 'fixed';
    margin-left: 20px;
    margin-right: auto;
}

.total-num {
    position: fixed;
    font-size: 2em;
    width: 100%;
    left: calc(50% - 1em);
}

.place-order-text {
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    bottom: auto;
    right: 20px;
}

@media only screen and (max-width: 633px) {
    .checkout-footer {
        height: 16vh;
        top: 84vh;
    }

    .total-container {
        height: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-evenly;
        font-size: 1.5em;
    }

    .total-num {
        font-size: 2em;
        left: calc(100vw - 10px - 2em);
    }

    .place-order-text {
        bottom: 5px;
        right: auto;
    }
}
.checkout {
    height: calc(100% - 16vh) !important;
}
.paypal-button-container {
    height: 5%;
}

@media only screen and (min-width: 633px) {

    .paypal-button-container {
        height: 10%;
    }

}
